body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.app {
  margin: 0;
  background-color: #ffffff;
  font-family: 'Roboto Mono', monospace; }

a {
  text-decoration: none; }

.app__main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center; }

.app__calendar {
  margin: 50px auto;
  box-sizing: border-box;
  width: 300px;
  height: 500px;
  border: 1px solid #f0f0f0;
  box-shadow: 0px 0px 10px 2px #f0f0f0; }
  @media all and (min-width: 768px) {
    .app__calendar {
      width: 500px;
      height: 600px; } }

.add__button {
  background-color: #3f9bd1;
  border: 1px solid #3f9bd1;
  font-weight: bold;
  width: 100%;
  height: 50px;
  font-size: 40px;
  color: #ffffff;
  padding: 0;
  margin: 0;
  border: none;
  outline: none;
  cursor: pointer; }

.add__button:hover {
  background-color: #45abe6; }

.calendar__container {
  margin: 30px 15px; }

.mood__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(auto-fill, 50px);
  grid-column-gap: 15px;
  grid-row-gap: 15px;
  -webkit-justify-content: center;
          justify-content: center; }

.mood__list-item {
  height: 50px;
  width: 50px;
  border-radius: 100px;
  display: grid;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center; }

.item--happy {
  background-color: #ffce00;
  color: #000000; }

.item--sad {
  background-color: #ff532c;
  color: #ffffff; }

.mood__text {
  font-weight: bold;
  font-size: 25px; }

.app__form-container {
  margin: 50px auto;
  box-sizing: border-box;
  width: 300px;
  height: 500px;
  border: 1px solid #f0f0f0;
  box-shadow: 0px 0px 10px 2px #f0f0f0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center; }
  @media all and (min-width: 768px) {
    .app__form-container {
      width: 500px;
      height: 600px; } }

.app__form {
  margin: 30px 0;
  width: 80%; }

.app__form-date {
  padding: 7px; }

.date-field {
  box-sizing: border-box;
  outline: none;
  display: block;
  width: 100%;
  padding: 7px;
  border: none;
  border-bottom: 1px solid #ddd;
  background: transparent;
  font-size: 14px;
  font-family: Arial, Helvetica, sans-serif;
  height: 30px; }

.app__form-state {
  padding: 7px; }

.radio__container {
  height: 30px;
  box-sizing: border-box;
  outline: none;
  display: block;
  width: 100%;
  padding: 7px;
  border: none;
  background: transparent;
  font-size: 14px;
  font-family: Arial, Helvetica, sans-serif;
  padding-left: 0; }

.label-sad {
  margin-left: 20px; }

.app__form-message {
  padding: 7px;
  display: block; }

.hidden {
  display: none; }

.message-field {
  box-sizing: border-box;
  outline: none;
  display: block;
  width: 100%;
  padding: 7px;
  border: none;
  border-bottom: 1px solid #ddd;
  background: transparent;
  font-size: 14px;
  font-family: Arial, Helvetica, sans-serif;
  height: 30px; }

.form__buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-top: 10px;
  padding: 7px; }

.button {
  width: 100%;
  height: 25px;
  text-transform: uppercase;
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 14px; }

.button-save {
  background-color: #3f9bd1;
  border: 1px solid #3f9bd1;
  color: #ffffff;
  font-weight: bold; }

.button-cancel {
  text-transform: capitalize;
  color: #a5a5a5; }

